import { mapToISOTimestamp } from "@rp/common/utils"
import { getWatchlistById, watchlistQueryKeys } from "@rp/react/tanstack-api/common"
import { useQuery } from "@tanstack/react-query"

const USE_QUERY_RETRY_DEFAULT = 3
// Get all watchlists
const useWatchlist = (watchlistId: string | undefined, disableRetry?: boolean) => {
  return useQuery({
    queryKey: watchlistQueryKeys.watchlist(watchlistId ?? ""),
    queryFn: ({ queryKey: [, id] }) => getWatchlistById(id),
    select: response => mapToISOTimestamp(response),
    enabled: !!watchlistId,
    refetchOnWindowFocus: false,
    retry: disableRetry ? false : USE_QUERY_RETRY_DEFAULT,
  })
}

export { useWatchlist }
